import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'

import Dashboard from './views/Dashboard.vue'
import Users from './views/users/users.vue'
import Categories from './views/categories/categories.vue'
import Products from './views/products/products.vue'
import Orders from './views/orders/orders.vue'
import Website from './views/website/website.vue'
import Wechat from './views/wechat/wechat.vue'

import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import User from './views/nav1/user.vue'



let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',                    // path
        component: Home,
        name: '',
        leaf: true,                   // 
        iconCls: 'fa fa-dashboard',   // : http://fontawesome.io
        children: [
            { path: '/', component: Dashboard, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-users',
        children: [
            { path: '/users', component: Users, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-folder-open',
        children: [
            { path: '/categories', component: Categories, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-cubes',
        children: [
            { path: '/products', component: Products, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-list',
        children: [
            { path: '/orders', component: Orders, name: ''}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-globe',
        children: [
            { path: '/web-config', component: Website, name: ''},
            { path: '/web-news', component: Website, name: ''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-wechat',
        children: [
            { path: '/wx-config', component: Wechat, name: ''},
            { path: '/wx-news', component: Wechat, name: ''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'UI',
        iconCls: '',
        children: [
            { path: '/table',component:Table, name: '' },
            { path: '/form', component: Form, name: '' },
            { path: '/user', component: User, name: '' },
        ]
    },

    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



